// Imposting nessesary elements
import React,{useEffect, useState} from "react";
import {Recipe} from "./Recipe";
import './App.css';
import {Footer} from "./Footer";
import {Head} from "./Head";

function App() {
    // Declaring the gloabal variables
    const APP_ID= "b523f5ad";
    const APP_KEY = "da2b70d3b5b13e748399b1d999202b6f";
    let intervalVar;

    // useState for change the recipes on search
    const [recipes, setRecipes] = useState([]);

    // useState for clearling the search box
    const[search, setSearch] = useState('');

    // useState for getting the queery of search
    const[query, setQuery] = useState('');
    useEffect( ()=>{
        getRecipes();
    },[query]);

    // Function for getting the recipes
    const getRecipes = async ()=>{

        // fethcing the API
        const response = await fetch(`https://api.edamam.com/search?q=${query}&app_id=${APP_ID}&app_key=${APP_KEY}`);



        if(document.getElementById('stats').textContent === "Search For recipe of any dish you want to cook"){
            
            // Preventing the loading stats if user haven't searched anything yet
            document.getElementById('stats').textContent = "Search For recipe of any dish you want to cook";
        }
        else{

            // Giving one more dot after in stats before response and after fetching
            document.getElementById('stats').textContent = "Loading..";
        }
        

        // Getting response into an variable
        const data = await response.json();



        // Giving the data to setrecipes useState to change the recepies
        setRecipes(data.hits);
        console.log(data.hits);


        // clearing the stats innerText after fethcing the data
        if(document.getElementById('stats').textContent === "Search For recipe of any dish you want to cook")
        {
        document.getElementById('stats').textContent = "Search For recipe of any dish you want to cook";
        }
        else{
            document.getElementById('stats').textContent = "";
            clearInterval(intervalVar);


        }}

    //Updating the searchbox/input value when usetype, (because for some reason it is on default set to not updating the textBox) 
    const updateSearch = e =>{
        setSearch(e.target.value);

    }

    // Getting the value of search box for sending request
    const getSearch = e =>{
        e.preventDefault();
        setQuery(search);
        setSearch("");
        document.getElementById('stats').innerHTML = "Loading<br></br><i>If this is taking much time then usual then please check your internet connect or try again after 1 min</i>";

    }

    return (
        <div className="App">

            {/* All content before footer */}
            <div className="mainBody">
                <Head />
                <form className="search-form" onSubmit={getSearch}>
                    <input className="search-bar" type="text" value={search} onChange={updateSearch}/>
                    <button className="search-button" type="submit">Search</button>
                </form>
                <div id="stats" className="stats">
                    Search For recipe of any dish you want to cook
                </div>
                <div className="recipes">

                {recipes.map(recipe =>(
                    <Recipe title={recipe.recipe.label}
                            key={recipe.recipe.label}
                            calories={recipe.recipe.calories}
                            image={recipe.recipe.image}
                            ingredients={recipe.recipe.ingredients}
                    />
                ))}
                </div>
            </div>


            {/* Footer */}
            <div>
                <Footer />
            </div>

        </div>
    );
}

export default App;
