import React from 'react';
import style from './recipe.module.css';


export const Recipe = ({ title, calories, image, ingredients }) => {

    return (
        <div className={style.recipe}>


            <h1 className="collapsible">{title}</h1>


            <div id="collapse" className={style.collapse}>
                <h3>Ingredients:</h3>

                <ol>
                    {ingredients.map(ingredient => (
                        <li>{ingredient.text}<hr></hr></li>
                    ))}
                </ol>
                <p><i>Total Calories:  </i>{Math.round(calories)}</p>

            </div>

            <img src={image} alt="" />

        </div>
    )
}
